form {
  width: 100%;
}

.form-container {
  padding:0 0 0 0;

  .form-title {
    padding: 5px 0 5px;
    font-size: 14px;
  }
  .user-input {
    width: 100%;
    height: 36px;
    border: 1px solid #e7e7e7;
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.13);
    border-radius: 10px;
    color: #000;
    line-height: 18px;
    background: none;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 18px;
    font-weight: 700;

  }

  .form-button {

    width:100%;
    margin:15px 0 0;

    display: flex;
    align-items: center;
    justify-content: center;
  }
}



