body {
    background: linear-gradient(to right, rgb(220, 255, 252), rgb(201, 232, 255));
}
.App {
    margin-top:50px;
    height: 100%;
}

html, body, #root {
    width: 100%;
    height: 100%;
    min-height: 100%;
}

.wrapper {
    position: relative;
    height:720px;

    .background{
        width: 100%;
        height: 120%;
        position: fixed;
        top: 20px;
        left: 0;
    }

    .first-page-content {
        z-index: 10;
        width: 100%;
        margin-top: 0;

        .overlay {
            margin-top:56px;
        }

        .hello-box {
            padding-top: 2vmax;
            text-align: center;
            font-size: 24px;
            @media (max-width: 768px) {
                text-align: center;
                font-size: 5vmin;
            }
        }

        .front-page__heading {
            margin: 4vmax 0;
            text-align: center;
            padding: 0 0 10px;
            font-size: 6vmax;
            font-weight: bold;
            text-emphasis-color: white;
            line-height: 1.2;
            text-shadow: 0 0.1vw 0.3vw #c80a00;
            @media (max-width: 768px) {
                font-size: 9vmax;
            }

        }
        .front-page__caption {
            margin: auto;
            text-align: center;
            padding: 0 0 2vmax;
            font-size: 3vmax;
            font-weight: bold;
            text-emphasis-color: white;
            line-height: 1.2;
        }
    }

    .center-row {
        display: flex;
        justify-content: center;
        margin-bottom: 10px;
        .buttons {
            padding: 10px 0;
            margin: 30px 0;
            z-index: 10;
            @media only screen and (max-device-width: 420px) {
                padding: 0;
                margin: 0;
                align-content: center;
            }
        }
        @media only screen and (max-device-width: 420px) {
            flex-direction: column;
            justify-content: space-around;
            text-align: center;
        }
        .trx-in-contract {
            text-align: center;
            border-color: #000;
            border-width: 1px;
            // background: white;
            opacity: 0.77;
            border-radius: 10px;
            margin: 15px;
            @media only screen and (max-device-width: 380px) {
                padding: 0;
                margin: 0;
                align-content: center;
            }

            .trx {
                margin: 10px;
                font-size:40px;
            }

            .caption {
                margin: 8px 0;

            }
        }


    }
}





.links {
    padding: 10px 0 16px;
}



.front-page__icons {
    text-align: center;

    .front-page__icons_each_block {
        border-color: #000;
        border-width: 1px;
        background: white;
        opacity: 0.77;
        border-radius: 10px;
        padding: 20px;
        margin: 10px;
        h4 {
            margin: 5px 0 0;
        }
    }

    img {
        width: 150px;
        height: 150px;
    }
}

.game-stats {
    margin: 30px 0 0 0;
    .game-stats__item {
        background: white;
        opacity: 0.77;
        border-radius: 10px;
        padding: 20px;
        margin: 10px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);

        h4 {
            text-align: center;
        }

        p {
            text-align: center;
            font-size: 40px;
            margin: 0;
        }
    }
}

.user-stats {
    margin-top: 5vh;
    position: relative;

    .investment {
        background: white;
        opacity: 0.77;
        border-radius: 10px;
        padding: 20px;
        margin: 10px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);

        .buy-title {
            padding:0 0 10px;
            font-size:24px;
            font-weight:bold;
            @media only screen and (max-device-width: 380px) {
                text-align: center;
            }
        }

        .available-and-invested {
            width: 100%;
            text-align: left;
            display: flex;
            align-items: center;
            border-radius: 8px;
            background-color: #f3f3f3;
            margin: 0 auto 0;

            .column {
                width:50%;
                float:left;
                padding: 7px 20px;
                border-right: 1px solid #ffffff;
            }

            .heading {
                font-size:13px;
            }
            .field {
                width:100%;
                font-size: 1.7vmax;
                padding:2px;
                font-weight: bold;
            }
        }
    }

    .sell-column {
        background: white;
        opacity: 0.77;
        border-radius: 10px;
        padding: 20px;
        margin: 10px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);

        .dividends-and-referral {
            width: 100%;
            text-align: right;
            display: flex;
            align-items: center;
            border-radius: 8px;
            background-color: #f3f3f3;
            margin: 0 auto 10px;

            .dividends {
                width:50%;
                padding: 7px 20px;
                border-right: 1px solid #ffffff;
            }
            .referral {
                width:50%;
                padding: 7px 20px;
                border-right: 1px solid #ffffff;
            }
            .heading {
                font-size:13px;
            }
            .field {
                width:100%;
                font-size: 1.7vmax;
                padding:2px;
                font-weight: bold;
            }
        }
        .sell-title {
            width:100%;
            padding:0 0 10px;
            font-size: 24px;
            font-weight:bold;

            @media only screen and (max-device-width: 380px) {
                text-align: center;
            }

        }
        .sell-button {
            width:100%;
            margin: 0 0;

            display: flex;
            align-items: center;
            justify-content: center;
        }


        .dividends-row {
            width: 100%;
        }

        .total-dividends {
            text-align: right;
            padding: 0 5px;

            span {
                margin-bottom: 12px;

            }

            .total-dividends-heading {
                position: relative;
                float: left;

                cursor: pointer;
                font-size:16px;
                @media only screen and (max-device-width: 380px) {
                    padding: 0;
                    margin-bottom: 0;
                }

            }
            .total-dividends-data {
                position: relative;
                font-size:40px;
                float: right;
                margin-bottom: 12px;
                @media only screen and (max-device-width: 380px) {
                    font-size: 30px;
                    float: left;
                }

            }

            &:after {
                content: attr(data-popuptext);
                background: black;
                border-radius: 3px;
                opacity: 0;
                top: 0;
                left: 0;
                position: absolute;
                margin-left:20px;
                transition: 200ms ease;
                white-space: nowrap;
                max-height: 0;
                color:white;

            }
            &:hover:after {
                opacity: 1;
                top: 1.2em;
                max-height: 200px;
                padding: .4em;
            }
        }


        .TRX {
            width:10%;
        }



    }


}
.earnings-column {
    background: white;
    opacity: 0.77;
    border-radius: 10px;
    padding: 20px;
    margin: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
    text-align: center;

    .title {
        font-size:24px;
        font-weight:bold;
    }
    .number--TRX {
        margin: 10px;
        font-size:28px;
    }
    .number--USD {
        font-size: 20px;
        margin: 10px;
    }
    .buttons {

    }
}

.referral-column {
    background: white;
    opacity: 0.77;
    border-radius: 10px;
    padding: 20px;
    margin: 2em 0;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
    text-align: center;

    .title {
        font-size:24px;
        font-weight:bold;
    }

    .address {
        width:100%;
        border-radius: 3px;

        line-height: 18px;
        line-break: loose;
        background: none;
        margin:10px 0 15px;
        font-size: 1.5vmax;
        .userReferralAddress {
            background: white;

        }
        .description {
            margin-top:1vmax;
            font-size: 1.3vmax;
            color: #949494;
        }

    }
}


.communities {
    background: white;
    opacity: 0.99;
    border-radius: 10px;
    padding: 20px;
    margin: 2em 0;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    justify-content: center;
    z-index: 12;
    .header {

        font-size: 2em;
        position: relative;
        text-align: center;

    }
    .logos {
        width:60px;
        height:60px;
        margin-bottom: 10px;
    }
    .ReactHover.Hover {
        width: 20px;
        background:white;
    }
}