.main {
  background: white;
  opacity: 0.99;
  border-radius: 10px;
  padding: 20px;
  margin: 2em 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  justify-content: center;
  z-index: 12;
}

.faq-and-fees--headings {
  width: 100%;
  overflow: auto;
  .FAQ {
    position: relative;
    float: left;
    font-size: 24px;
    font-weight: bold;
  }

  .fees {
    position: relative;
    float: right;
    font-size: 24px;
    font-weight: bold;
  }
}
.heading-left {
  text-align: left;
}

.body {
  text-align: left;
}


  @media (min-width: 769px) {
    .fees-content {
      text-align: left;
    }
}
