.tronLink {
    background: white;
    opacity: 0.77;
    border-radius: 10px;
    padding: 20px;
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);

    @media (max-width: 768px) {
        flex-direction: column;
        text-align: center;
    }

    align-items: center;
    h1 {
        font-size:2rem;
        margin: 0;
        margin-bottom: 10px;
    }

    p {
        padding: 0;
        margin: 0;
    }

    .info {
        flex: 1;
    }

    .logo {
        margin-left: 30px;

        @media (max-width: 768px) {
            margin-left: 0;
            margin-top: 2vmin;
        }
    }
}

.tronLink.hover {
    transition: all 200ms ease;
    cursor: pointer;
}

